import React from 'react';
import ReactDOM from 'react-dom';
import $ from 'jquery';
import * as swa from 'swa';

import { Application, LayoutsButton, MiscMenuButton, PanelsButton, UndoButton, QueryStatusIndicator } from 'TB';

document.addEventListener('readystatechange', (event) => {
    if (event.target.readyState === 'interactive') {
        let parent = $('#ApplicationParent');
        if (parent.length > 0) {
            initApplication(parent);
        }
    }
});

const initApplication = (parent) => {

    let dataService = parent.attr('data-init-service');

    if (!dataService || dataService.length == 0) {
        dataService = 'teamwork:GraphInitDataJSON';
    }

    // Note: Some links include both viewPointClass and resourceType, where resourceType is a subclass of viewPointClass.
    //       Here we only use viewPointClass, yet in theory the Search Panel could also pre-select the resourceType
    //       while allowing the tree to start at the viewPointClass.
    let params = {};
    let url = new URL(window.location);
    let viewPointClass = url.searchParams.get('viewPointClass');
    if(viewPointClass && viewPointClass != '') {
        params.mainClassOverride = viewPointClass;
    }

    swa.get(dataService, params).then((data) => {
        if (data.data.workflowId) {
            $('#appBody').css('height', 'calc(100vh - 100px)');
        }

        let appRef = React.createRef();
        let panelsButtonRef = React.createRef();

        let app = <Application graphInitData={data.data} ref={appRef} panelsButtonRef={panelsButtonRef} />;

        ReactDOM.render(app, parent[0]);
        let papa = $('#HeaderButtonParent');

        if (papa.length > 0) {
            ReactDOM.render(
                <div className="btn-toolbar">
                    <QueryStatusIndicator appRef={appRef} />
                    <LayoutsButton appRef={appRef} />
                    <PanelsButton appRef={appRef} ref={panelsButtonRef} />
                    <MiscMenuButton appRef={appRef} />
                    <UndoButton appRef={appRef} />
                </div>,
                papa[0]
            );
        }
    });
};
